class CommandGroup extends React.Component {
    constructor(props) {
        super(props);

        this.handleClick = this.handleClick.bind(this);

        this.state = {
            action: 'open',
            displayed: false
        };
    };

    handleClick() {
        if (this.state.action === 'open') {
            this.setState({
                displayed: true,
                action: 'close'
            });
        } else if (this.state.action === 'close') {
            this.setState({
                displayed: false,
                action: 'open'
            });
        };
    };

    render() {
        let content;

        if (this.state.displayed === true) {
            content = (
                <div className={styles.content}>
                    {this.props.children}
                </div>
            );
        } else if (this.state.displayed === false) {
            content = (
                <></>
            );
        };

        return (
            <div className={styles.CommandGroup}>
                <h1 className={styles.name} onClick={this.handleClick}>
                    {this.props.name} (<code className={styles.module}>{this.props.module}</code>)
                </h1>
                {content}
            </div>
        );
    };
};

class CommandList extends React.Component {
    render() {
        return (
            <table className={styles.CommandList}>
                <thead>
                    <tr>
                        <td>
                            {this.props.h1}
                        </td>
                        <td>
                            {this.props.h2}
                        </td>
                        <td>
                            {this.props.h3}
                        </td>
                        <td>
                            {this.props.h4}
                        </td>
                    </tr>
                </thead>
                <tbody>
                    {this.props.children}
                </tbody>
            </table>
        );
    };
};

class Command extends React.Component {
    render() {
        return (
            <tr>
                <td>
                    {this.props.state}
                </td>
                <td className={styles.c}>
                    <code>
                        {this.props.command}
                    </code>
                </td>
                <td className={styles.c}>
                    <code>
                        {this.props.module}
                    </code>
                </td>
                <td>
                    {this.props.description}
                </td>
            </tr>
        );
    };
};

